import { Component, Input } from '@angular/core';

@Component({
  selector: 'lib-hello',
  template: `<p>Привет, {{ name }}!</p>`,
  styles: []
})
export class HelloComponent {
  @Input() name: string = '';

  public postMessage() {
    window.postMessage(JSON.stringify({
      event: "promotions",
      data: {},
      requestId: '00-575d6b495c1355ec5bf66ae432b601ef-2b0d7c6e513397e2-01',
      action: 'promise'
    }), '*');
  }
}